/* /////////// social media icon //////////// */

.socialMedia-icon {
    position: absolute;
    height: 200px;
    width: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.108);
    z-index: 10;
    position: fixed;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    margin-top: 300px;
}

.socialMedia-icon>a>img {
    height: 30px;
    width: 30px;
    margin: 4px 3px;
}

.socialMedia-icon>a>img:hover {
    -webkit-transform: scale(1.10);
            transform: scale(1.10);
}

@media only screen and (max-width: 750px) {
    .socialMedia-icon {
        display: none;
    }
}

