    padding: 14px 35px;
    border: 2px solid rgb(32, 182, 154);
    border: 2px solid rgb(32, 182, 154);
    background-color: black;
    color: white;
    transition: 0.5s;
    word-spacing: 4px;
    /* margin: 10px 40px; */
    margin-right: 100px;
    cursor: pointer;
}

.Contact__button:hover {
    /* background-color: rgb(255, 255, 255); */
    border-color: rgb(32, 182, 154);
    border: 2px soild white;
    color: rgb(0, 0, 0);
    color: white;
    color: rgb(32, 182, 154);
}

@media only screen and (max-width: 900px) {
    .Contact {
        height: 40vh;
        width: 100%;
        white-space: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: black;
        flex-direction: column;
    }
    .Contact__sub1 {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .Contact__sub1>h1 {
        font-size: 25px;
        font-weight: 800;
        color: white;
        padding: 10px;
    }
    .Contact__sub1>h1>span {
        font-size: 25px;
        font-weight: 500;
        color: black;
        /* margin: 10px; */
        text-shadow: -1px 1px 0 rgb(255, 255, 255), 1px 1px 0 rgb(255, 255, 255), 1px -1px 0 rgb(255, 255, 255), -1px -1px 0 rgb(255, 255, 255);
    }
    .Contact__sub1>h6 {
        margin: 20px 0 0 30px;
    }
    .Contact__button {
        width: 180px;
        height: 40px;
        font-size: 16px;
        padding: 5px 12px;
        border: 1px solid white;
        background-color: black;
        color: white;
        transition: 1s;
        word-spacing: 4px;
        margin: 10px;
        border-radius: 50px;
    }
}
